import React from "react"
import { graphql } from "gatsby"
import { Button } from "gatsby-theme-material-ui"
import { makeStyles } from "@material-ui/core/styles"
import Typography from "@material-ui/core/Typography"
import Grid from "@material-ui/core/Grid"
import Divider from "@material-ui/core/Divider"
import ButtonGroup from "@material-ui/core/ButtonGroup"
import { MDXRenderer } from "gatsby-plugin-mdx"
import ArrowBackIcon from "@material-ui/icons/ArrowBack"
import ArrowForwardIcon from "@material-ui/icons/ArrowForward"
import Sticky from "react-stickynode"

// import Bio from "../components/Bio"
import Layout from "../components/Layout"
import SEO from "../components/SEO"
import Rating from "../components/Rating"
import Post from "../components/Post"

const useStyles = makeStyles(theme => ({
  grow: {
    flexGrow: 1,
  },
  nav: {
    marginTop: theme.spacing(2),
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
  },
}))

const BlogPostTemplate = ({ data, location }) => {
  const classes = useStyles()
  const post = data.mdx
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const { previous, next, recent } = data

  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
        keywords={post.frontmatter.tags}
      />
      <Grid
        container
        direction="row"
        justify="space-between"
        alignItems="stretch"
        spacing={2}
      >
        <Grid
          item
          component="article"
          itemScope
          itemType="http://schema.org/Article"
          md={9}
        >
          <header>
            <ButtonGroup variant="contained" color="secondary">
              {post.frontmatter.tags?.map(tag => {
                return (
                  <Button key={tag} to={`/tag/${tag.toLowerCase().trim()}`}>
                    {tag}
                  </Button>
                )
              })}
            </ButtonGroup>
            <Typography itemProp="headline" variant="h1" component="h1">
              {post.frontmatter.title}
            </Typography>
            <Typography itemProp="dateCreated" component="p">
              {post.frontmatter.date}
            </Typography>
          </header>
          <MDXRenderer>{post.body}</MDXRenderer>
          {post.frontmatter.rating && (
            <Rating ratings={post.frontmatter.rating} />
          )}
          {/*<footer>
          <Bio />
        </footer> */}
        </Grid>
        {recent && (
          <Grid item md={3}>
            <Sticky top="#AppBar" bottomBoundary="#main">
              <Typography variant="h3">Recent Posts:</Typography>
              <Grid container spacing={2}>
                {recent.edges.map(({ node }, i) => {
                  return (
                    <Grid key={i} item>
                      <Post node={node} />
                    </Grid>
                  )
                })}
              </Grid>
            </Sticky>
          </Grid>
        )}
      </Grid>
      {(previous || next) && (
        <nav className={classes.nav}>
          <Divider />
          <Grid container className={classes.grow} spacing={2}>
            <Grid item>
              {previous && (
                <Button
                  color="primary"
                  to={previous.fields.slug}
                  rel="previous"
                  startIcon={<ArrowBackIcon />}
                >
                  {previous.frontmatter.title}
                </Button>
              )}
            </Grid>
            <Grid item className={classes.grow} />
            <Grid item>
              {next && (
                <Button
                  color="primary"
                  to={next.fields.slug}
                  rel="next"
                  endIcon={<ArrowForwardIcon />}
                >
                  {next.frontmatter.title}
                </Button>
              )}
            </Grid>
          </Grid>
        </nav>
      )}
    </Layout>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    mdx(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      body
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        tags
        rating {
          characters
          plot
          entertainment
        }
      }
    }
    previous: mdx(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: mdx(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    recent: allMdx(
      sort: { fields: frontmatter___date, order: ASC }
      limit: 6
      filter: { fields: { type: { in: ["book-reviews", "blog"] } } }
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "DD MMMM, YYYY")
            title
            description
          }
        }
      }
    }
  }
`
